.header-container {
    
    position:relative;
    background-image: url('../../../images/car.jpg');
    background-size: cover;
    background-repeat: no-repeat;
     /* z-index: -1; */
}
.header-container::after {
    content:' ';
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    right: 0;
    background-color: #3A4256;
    opacity: 0.4;
    /* z-index: 1; */
}